
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { allBooks, userBookAccess } from "@/lib/sample-data";
import { Book, Compass, Lightbulb, BookHeart } from "lucide-react";
import Image from "next/image";
import Link from "next/link";

export default function ReaderDashboard() {
  const libraryBooks = allBooks.filter((book) =>
    userBookAccess.some((access) => access.bookId === book.id)
  );

  return (
    <div className="container mx-auto px-4 py-12">
      <div className="mb-8">
        <h1 className="text-4xl font-bold font-headline">Reader Dashboard</h1>
        <p className="text-muted-foreground mt-2">
          Welcome back, Reader! Here's your personalized space.
        </p>
      </div>

      <div className="grid gap-8 lg:grid-cols-3">
        {/* Main content - Library and Recommendations */}
        <div className="lg:col-span-2 space-y-8">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center">
                <BookHeart className="mr-3" />
                Continue Reading
              </CardTitle>
            </CardHeader>
            <CardContent>
              {libraryBooks.length > 0 ? (
                <div className="flex gap-6 items-center">
                  <Image
                    src={libraryBooks[0].imageUrl}
                    alt={libraryBooks[0].title}
                    width={120}
                    height={180}
                    className="rounded-md object-cover aspect-[2/3]"
                    data-ai-hint="book cover"
                  />
                  <div>
                    <h3 className="text-xl font-headline font-semibold">
                      {libraryBooks[0].title}
                    </h3>
                    <p className="text-muted-foreground mb-4">
                      by {libraryBooks[0].author}
                    </p>
                    <Button asChild>
                      <Link href={`/books/${libraryBooks[0].id}`}>
                        Jump Back In
                      </Link>
                    </Button>
                  </div>
                </div>
              ) : (
                <p className="text-muted-foreground">
                  You haven't purchased any books yet.
                </p>
              )}
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>My Library</CardTitle>
              <CardDescription>
                An overview of your purchased books.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4">
                {libraryBooks.slice(0, 4).map((book) => (
                  <Link key={book.id} href={`/books/${book.id}`}>
                    <Image
                      src={book.imageUrl}
                      alt={book.title}
                      width={150}
                      height={225}
                      className="rounded-md object-cover aspect-[2/3] transition-all hover:opacity-80"
                      data-ai-hint="book cover"
                    />
                  </Link>
                ))}
              </div>
              {libraryBooks.length === 0 && (
                <p className="text-muted-foreground">Your library is empty.</p>
              )}
            </CardContent>
            {libraryBooks.length > 4 && (
              <CardFooter>
                 <Button variant="outline" asChild>
                    <Link href="/reader/library">View Full Library</Link>
                 </Button>
              </CardFooter>
            )}
          </Card>
        </div>

        {/* Sidebar */}
        <div className="lg:col-span-1 space-y-8">
          <Card className="bg-secondary/50">
            <CardHeader>
              <CardTitle className="flex items-center">
                <Compass className="mr-3" />
                Explore
              </CardTitle>
            </CardHeader>
            <CardContent className="flex flex-col gap-4">
              <Button asChild size="lg">
                <Link href="/books">Browse All Books</Link>
              </Button>
              <Button asChild variant="outline" size="lg">
                <Link href="/reader/library">Go to My Library</Link>
              </Button>
            </CardContent>
          </Card>
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center">
                <Lightbulb className="mr-3" />
                AI Recommendations
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-muted-foreground mb-4">
                Get personalized book suggestions based on your taste.
              </p>
              <Button asChild className="w-full">
                <Link href="/ai/suggestions?role=reader">
                  Find My Next Read
                </Link>
              </Button>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
